import { Component } from '@angular/core';
import { RequestsState } from 'app/services/requests-state';
import { ReputationService } from 'app/services/reputation';
import { TranslateService } from 'app/utils/translate/translate.service';
import { PermissionsService } from 'app/services/permissions';
import { Product } from '@imunify360-api/license';
import { LicenseState } from 'app/services/license-state';


@Component({
    selector: 'reputation',
    styleUrls: ['./reputation.component.scss'],
    templateUrl: './reputation.component.html',
})
export class ReputationComponent {
    Product = Product;
    constructor(
        public translate: TranslateService,
        public requestsState: RequestsState,
        public reputationService: ReputationService,
        public permissions: PermissionsService,
        public licenseState: LicenseState,
    ) { }
    defaultTranslation(type: string) {
        // no optimization since it is very rare case
        return type[0].toUpperCase() + type.slice(1).toLowerCase().replace(/_/g, ' ');
    }
}
