import { Component, ContentChild, Input, TemplateRef } from '@angular/core';

/**
 * implementation via pipe will be more complex because translations are async
 * and we would not be able to lazy-load `defaultTemplate`
 */
@Component({
    selector: 'i360-default-if-not-translated',
    template: `
        <ng-template #helper
                     let-translated>
            <ng-template [ngIf]="key === translated" [ngIfElse]="foundKeyTemplate">
                <ng-template [ngTemplateOutlet]="defaultTemplate"></ng-template>
            </ng-template>
            <ng-template #foundKeyTemplate>
                {{ translated }}
            </ng-template>
        </ng-template>
        <ng-container *ngTemplateOutlet="helper; context: {$implicit: key | translate}">
        </ng-container>
    `,
})
export class DefaultIfNotTranslatedComponent {
    @Input() key: string;
    @ContentChild('default', {static: true}) defaultTemplate: TemplateRef<any>;
}
